body {
    margin: 0;
    font-family: $secondary-font;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.7;
}
body * {
    margin: 0;
}
h2 {
    @extend %headline-3-bold;        
}
h3 {
    @extend %headline-4-semibold;
}
h4 {
    @extend %headline-5-semibold;
}
h5 {
    @extend %headline-6-semibold;    
}
h6 {
    @extend %subtitle-2-semibold;
}
.capaitalised {
    @extend %capaitalised;
}
.container {
    max-width: 1044px;
    margin: auto;
    @media (max-width: 1200px) {
        padding-left: 60px;
        padding-right: 60px;
    }
    @media (max-width: 700px) {
        padding-left: 15px;
        padding-right: 15px;
    }
}
.columns {
    display: flex;
}
.button {
    @extend %label-1-semibold;
    display: inline-block;
    background-color: $deep-blue;
    border: 0;
    padding: 14px 24px;
    border-radius: 8px;
    color: $pure-white;    
    text-decoration: none;
    transition: .2s;
    cursor: pointer;
    &:hover {
        background-color: $deep-blue-hover;
        box-shadow: 0 10px 12px $deep-blue-shadow;
    }
    &:active {
        background-color: $deep-blue;
    }
    @media (max-width: 1200px) {
        padding: 10px 20px;
        font-size: 1em;
    }
    &_outline {
        padding: 12px 22px;
        background: none;
        border: 2px solid $pure-white;
        @media (max-width: 1200px) {
            padding: 8px 18px;
        }
    }
}